"use client";

import { useState } from "react";
import Link from "next/link";
import { FaFolder, FaChevronRight } from "react-icons/fa";

export default function ManageItemPage() {
  const menuItems = [
    {
      id: 1,
      name: "Categories",
      path: "/manageitem/categories",
      icon: "üìÅ",
      description: "Manage product categories",
      color: "bg-blue-500"
    },
    {
      id: 2,
      name: "Lead Source",
      path: "/manageitem/lead-source",
      icon: "üéØ",
      description: "Manage lead sources",
      color: "bg-green-500"
    },
    {
      id: 3,
      name: "Lead Status",
      path: "/manageitem/lead-status",
      icon: "üìä",
      description: "Manage lead status",
      color: "bg-purple-500"
    },
    {
      id: 4,
      name: "Products",
      path: "/manageitem/products",
      icon: "üì¶",
      description: "Manage products",
      color: "bg-orange-500"
    }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 p-4 md:p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-md p-6 mb-8">
          <h1 className="text-3xl md:text-4xl font-bold text-gray-800 mb-2">
            Manage Items
          </h1>
          <p className="text-gray-600">
            Select a category to manage your data
          </p>
        </div>

        {/* Menu Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {menuItems.map((item) => (
            <Link
              key={item.id}
              href={item.path}
              className="group bg-white rounded-xl shadow-md hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1 overflow-hidden"
            >
              <div className={`${item.color} h-2`}></div>
              
              <div className="p-6">
                <div className="flex items-center justify-between mb-4">
                  <div className="text-4xl">{item.icon}</div>
                  <FaChevronRight className="text-gray-400 group-hover:text-gray-600 group-hover:translate-x-1 transition-all" />
                </div>

                <h3 className="text-xl font-semibold text-gray-800 mb-2 group-hover:text-blue-600 transition-colors">
                  {item.name}
                </h3>

                <p className="text-sm text-gray-600">
                  {item.description}
                </p>
              </div>

              <div className="px-6 pb-4">
                <div className="flex items-center text-sm text-blue-600 font-medium">
                  <span>Manage</span>
                  <FaChevronRight className="ml-1 text-xs" />
                </div>
              </div>
            </Link>
          ))}
        </div>

        {/* Quick Stats */}
        <div className="mt-8 bg-white rounded-lg shadow-md p-6">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">
            Quick Overview
          </h2>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-blue-50 rounded-lg">
              <div className="text-2xl font-bold text-blue-600">--</div>
              <div className="text-sm text-gray-600 mt-1">Categories</div>
            </div>
            <div className="text-center p-4 bg-green-50 rounded-lg">
              <div className="text-2xl font-bold text-green-600">--</div>
              <div className="text-sm text-gray-600 mt-1">Lead Sources</div>
            </div>
            <div className="text-center p-4 bg-purple-50 rounded-lg">
              <div className="text-2xl font-bold text-purple-600">--</div>
              <div className="text-sm text-gray-600 mt-1">Lead Status</div>
            </div>
            <div className="text-center p-4 bg-orange-50 rounded-lg">
              <div className="text-2xl font-bold text-orange-600">--</div>
              <div className="text-sm text-gray-600 mt-1">Products</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}   